// SECTION iteration :: START

.iteration {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  max-height: 100vh;

  &__slide {
    z-index: 5;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
    max-height: 100vh;
    padding: 75px 15px;
    @include adaptive(1023) {
      padding-left: 25px;
      padding-right: 25px;
    }
    @include adaptive(767) {
      height: auto;
      max-height: unset;
      padding-left: 15px;
      padding-right: 15px;
    }
    @include adaptive(424) {
      padding-top: 50px;
      padding-bottom: 50px;
    }

    > div {
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      max-height: 100%;

      img {
        max-height: 100%;
      }
    }

    &--image {
      img {
        z-index: 2;
        position: relative;
        display: block;
        max-width: 100%;
        height: auto;
        max-height: 100%;
        margin-left: auto;
        margin-right: auto;
      }
    }

    &--text {
      p {
        z-index: 2;
        position: relative;
        width: 100%;
        max-width: 898px;
        margin-left: auto;
        margin-right: auto;
        font-weight: bold;
        font-size: 52px;
        color: #9b9b9b;
        @include adaptive(1439) {
          font-size: 44px;
        }
        @include adaptive(1365) {
          font-size: 40px;
        }
        @include adaptive(1279) {
          font-size: 36px;
        }
        @include adaptive(1023) {
          max-width: 600px !important;
          font-size: 33px;
        }
        @include adaptive(767) {
          max-width: 100% !important;
          font-size: 26px;
        }

        span {
          color: #000000;
        }
      }
    }

    &--1 {
      @include adaptive(767) {
        padding-top: 150px;
      }
    }

    &--2 {
      padding-left: 120px;
      padding-right: 354px;
      @include adaptive(1439) {
        padding-left: 70px;
        padding-right: 304px;
      }
      @include adaptive(1279) {
        padding-right: 244px;
      }
      @include adaptive(1023) {
        padding-right: 15px;
        padding-left: 15px;
      }
      @include adaptive(767) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &--4 {
      padding-left: 368px;
      padding-right: 250px;
      @include adaptive(1439) {
        padding-left: 318px;
        padding-right: 200px;
      }
      @include adaptive(1279) {
        padding-left: 258px;
        padding-right: 140px;
      }
      @include adaptive(1023) {
        padding-left: 100px;
        padding-right: 90px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &--5,
    &--27,
    &--33 {
      .iteration {
        &__bg {
          &-path {
            top: 50%;
            right: -36%;
            transform: translateY(-50%);
            width: 1298px;
            height: 1298px;
            border-radius: 50%;
            background-color: #fafafa;
            @include adaptive(1279) {
              width: 1024px;
              height: 1024px;
            }
            @include adaptive(1023) {
              left: 56%;
              right: auto;
            }
            @include adaptive(767) {
              width: 600px;
              height: 600px;
            }
          }
        }
      }
    }

    &--5 {
      padding-right: 320px;
      @include adaptive(1439) {
        padding-right: 170px;
      }
      @include adaptive(1279) {
        padding-right: 110px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &--6 {
      padding-right: 303px;
      @include adaptive(1439) {
        padding-right: 253px;
      }
      @include adaptive(1279) {
        padding-right: 193px;
      }
      @include adaptive(1023) {
        padding-right: 143px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }

      p {
        max-width: 1040px;
        @include adaptive(1279) {
          max-width: 898px;
        }
      }
    }

    &--7,
    &--8,
    &--10,
    &--11,
    &--13,
    &--14,
    &--15,
    &--17,
    &--19,
    &--20,
    &--23,
    &--24,
