        padding-right: 15px;
      }
    }

    &--22 {
      padding-right: 241px;
      @include adaptive(1439) {
        padding-right: 191px;
      }
      @include adaptive(1279) {
        padding-right: 131px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &--27 {
      padding-left: 135px;
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }

      .iteration {
        &__bg {
          &-path {
            right: -20%;
            @include adaptive(767) {
              left: 50%;
              right: auto;
            }
          }
        }
      }
    }

    &--28 {
      p {
        max-width: 1180px;
        @include adaptive(1279) {
          max-width: 898px;
        }
      }
    }

    &--29 {
      .iteration {
        &__title {
          padding-left: 227px;
          @include adaptive(1279) {
            padding-left: 167px;
          }
          @include adaptive(1023) {
            padding-left: 80px;
          }
          @include adaptive(767) {
            padding-left: 0;
          }
        }
      }
    }

    &--30 {
      p {
        max-width: 1420px;
        @include adaptive(1279) {
          max-width: 898px;
        }
      }
    }

    &--32 {
      padding-right: 158px;
      @include adaptive(1439) {
        padding-right: 108px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }
    }

    &--33 {
      padding-left: 110px;
      padding-right: 59px;
      @include adaptive(1439) {
        padding-left: 60px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }

      .iteration {
        &__bg {
          &-path {
            left: 0;
            right: auto;
            @include adaptive(767) {
              left: 60%;
            }
          }
        }
      }
    }

    &--34 {
      padding-left: 210px;
      padding-right: 170px;
      @include adaptive(1439) {
        padding-left: 160px;
        padding-right: 120px;
      }
      @include adaptive(1023) {
        padding-left: 15px;
        padding-right: 15px;
      }

      @include adaptive(767) {
        padding-bottom: 150px;
      }

      .iteration {
        &__bg {
          img {
            position: absolute;

            &:nth-of-type(1) {
              top: 100px;
              left: 100px;
              @include adaptive(767) {
                top: 50px;
                left: 50px;
                transform: scale(0.75);
              }
            }

            &:nth-of-type(2) {
              right: -100px;
              bottom: -100px;
              @include adaptive(767) {
                transform: scale(0.5);
                right: -160px;
                bottom: -160px;
              }
            }
          }
        }
      }
    }
  }

  &__bg {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;

    &-path {
      position: absolute;
    }
  }

  &__title {
    z-index: 0;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-weight: bold;
    font-size: 350px;
    line-height: 341px;
    color: #fafafa;
    @include adaptive(1534) {
      font-size: 300px;
      line-height: 300px;
    }
    @include adaptive(1439) {
      font-size: 250px;
      line-height: 250px;
    }
    @include adaptive(1335) {
      font-size: 200px;
      line-height: 190px;
    }
    @include adaptive(1279) {
      font-size: 150px;
      line-height: 140px;
    }
    @include adaptive(1023) {
      font-size: 100px;
      line-height: 110px;
    }
  }

  &__advantages {
    width: 100%;
    max-width: calc(100% / 3);
    @include adaptive(767) {
      max-width: 100%;
      padding-bottom: 30px;
    }

