        width: 100%;
      }
      &:nth-of-type(3) {
        width: 1383px;
        @include adaptive(1279) {
          width: 1024px;
        }
      }
      &:nth-of-type(12) {
        width: 1250px;
        @include adaptive(1439) {
          width: 1000px;
        }
      }
      &:nth-of-type(23) {
        width: 1050px;
      }
      &:nth-of-type(33) {
        width: 1400px;
        @include adaptive(1279) {
          width: 1100px;
        }
      }
    }
